.author {
  display: flex;

  img {
    height: 3rem;
    width: 3rem;
    flex: 0 0 3rem;
    margin-right: 1rem;

    @include media-breakpoint-up(lg) {
      height: 4rem;
      width: 4rem;
      flex: 0 0 4rem;
    }
  }

  h6 {
    margin: 0.5rem 0 0.25rem 0;
  }
}



